<template>
    <div>
        <el-form :model="dataSource" :rules="rules" ref="dataSource" label-width="120px">
            <el-row :gutter="20">
                <el-col :span="7">
                    <el-form-item label="" prop="ip">
                        <el-input v-model="dataSource.ip" placeholder="ip"></el-input>
                    </el-form-item>
                </el-col>
                <el-col :span="7">
                    <el-form-item label="PORT" prop="port" required>
                        <el-input v-model="dataSource.port"></el-input>
                    </el-form-item>
                </el-col>
                <el-col :span="7">
                    <el-form-item label="" prop="tableName">
                        <el-input v-model="dataSource.tableName"></el-input>
                    </el-form-item>
                </el-col>
            </el-row>
            <el-row :gutter="20">
                <el-col :span="7">
                    <el-form-item label="" prop="dbname">
                        <el-input v-model="dataSource.dbname"></el-input>
                    </el-form-item>
                </el-col>
                <el-col :span="7">
                    <el-form-item label="" prop="username">
                        <el-input v-model="dataSource.username"></el-input>
                    </el-form-item>
                </el-col>
                <el-col :span="7">
                    <el-form-item label="" prop="password">
                        <el-input v-model="dataSource.password" type="password"></el-input>
                    </el-form-item>
                </el-col>
            </el-row>
        </el-form>
    </div>
</template>

<script>
export default {
  props: ['dataSource', 'indexOperation'],
  data() {
    return {
      rules: {
        ip: [{ required: true, message: '', trigger: 'blur' }],
        port: [{ validator: this.$validate.validatePort, trigger: 'blur' }],
        username: [{ required: true, message: '', trigger: 'blur' }],
        password: [{ required: true, message: '', trigger: 'blur' }],
        dbname: [{ required: true, message: '', trigger: 'blur' }, { validator: this.dbnameValidate, trigger: 'blur' }],
        tableName: [{ required: true, message: '', trigger: 'blur' }, { validator: this.dbnameValidate, trigger: 'blur' }],
      },
    };
  },
  methods: {
    dbnameValidate(rule, value, callback) {
      this.$emit('db-validate', this.dataSource, callback.bind(this));
    },
  },
};
</script>
<style>
</style>




